<template>
  <div>

    <page-loading v-if="loading"></page-loading>
    <no-data v-if="!loading && writings.length === 0" message="No podcast added"></no-data>


    <div v-if="!loading" class="row">

      <div class="col col-md-12 col-sm-12 col-12">
        <div class="row">
          <div :key="writing.id" v-for="writing in writings" class="col col-md-3 col-sm-12 col-12 mt-6">
            <vx-card>
              <img
                @click="goWritingResult(writing.id)"
                src="@/assets/images/custom/writing-thumbnail.png"
                alt="content-img"
                class="responsive rounded-lg cursor-pointer">


              <div class="text-center" style="height: 50px">
                <h5 @click="goWritingResult(writing.id)" class="cursor-pointer" style="direction: ltr">{{ writing.question }}</h5>
              </div>
              <vs-divider></vs-divider>
              <div class="flex justify-end flex-wrap">
                  <vs-button @click="goWritingResult(writing.id)"  class="mt-4 mr-2" type="border" >View Answer</vs-button>
              </div>
            </vx-card>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "../../../http/axios";
import mixins from "../../../mixins";

const PAGE_SIZE = 25;
export default {
  name: "WritingList",

  mixins: [mixins],
  data() {
    return {
      writings: [],
      loading: true,
    }
  },
  mounted() {
    this.getWritings();
  },
  methods: {
    getWritings() {

      this.loading = true;

      this.writings = [];
      let url = `/api/english/writings`
      axios.get(url, {
        headers: {
          Accept: "application/json",
          Authorization: this.getAccessToken()
        }
      }).then(res => {
        if (res.status === 200) {
          console.log(res);
          this.writings = res.data;
        } else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger');
        }
        this.loading = false;

      })
    },

    goWritingResult(writingId) {
      this.$router.push({
        name: "writing-result",
        query: {
          writingId: writingId,
        }
      })
    },

  }
}
</script>

